import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, TextField, SearchLocationField } from '../components';

import { FacilityCreate } from '../api';
import { useSnackbar } from '../hooks';
import { CreateFacility, GeoPoint } from '../utils/dataTypes';

const validationSchema = yup.object({
  name: yup.string().required('Facility name is required'),
  address: yup.string().required('Address is required'),
  address2: yup.string(),
  facilityLocation: yup
    .tuple([
      yup
        .number()
        .typeError('Latitude is required')
        .min(-90, 'Latitude must be grater than or equal: -90')
        .max(90, 'Latitude must be less than or equal: 90')
        .required('Location should be in format "latitude, longitude"'),
      yup
        .number()
        .typeError('Longitude is required')
        .min(-180, 'Longitude must be grater than or equal: -180')
        .max(180, 'Longitude must be less than or equal: 180')
        .required('Location should be in format "latitude, longitude"'),
    ])
    .required('Location is required'),
});

const initialValues = {
  name: undefined,
  address: undefined,
  address2: undefined,
  facilityLocation: [NaN, NaN] as GeoPoint,
};

export default function CreateFacilityForm() {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(FacilityCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['facilities']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateFacility>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateFacility);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="name"
                label="Facility Name"
                formik={formik}
              />
              <TextField
                fieldName="address"
                label="Address Line 1"
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SearchLocationField
                fieldName="facilityLocation"
                label="Location"
                formik={formik}
              />
              <TextField
                fieldName="address2"
                label="Address Line 2"
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
